@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
}

#root, .App {
  height: 100%;
}
.App {
  font-family: 'Poppins', sans-serif;
}

.home {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
}

.sidebar-left {
  display: flex;
  background: #000000;
  width: 56px;
  height: 100%;
}

.logo-holder {
  width: 56px;
  height: 56px;
  background: #0061FE;
}

.sidebar-logo {
  position: absolute;
  top: 11px;
  left: 11px;
}

.content-single {
  background: #14C8EB;
  justify-content: center;
  align-items: center;
}

.title.single-title.no-padding {
  padding: 0;
}

.title.single-title {
  color: #F7F5F2;
  padding-bottom: 45px;
  text-align: center;
}

.content {
  display: flex;
  flex: 1 1;
  flex-direction: column;
}

.content-top {
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title {
  color: #1E1919;
  width: 626px;
  font-size: 72px;
  line-height: 79px;
  letter-spacing: -0.01em;
  padding: 0;
  margin: 0;
  font-style: normal;
  font-weight: 300;
  text-align: left;

}

.single-title.page-subtitle {
  color: #F7F5F2;
  padding-bottom: 31px;
  text-align: center;
  font-size: 20px;
  line-height: 30px;
}

.page-subtitle {
  color: #1E1919;
  width: 626px;
  font-size: 16px;
  line-height: 24px;
  text-align: left;
}

.try-link {
  color: #0061FE;
  font-weight: bolder;
  text-decoration: underline;
  background: transparent;
  border: 0;
}

.content-bottom {
  width: 100%;
  height: 299px;
  background: #14C8EB;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sidebar-right {
  display: flex;
  background: #E5E5E5;
  height: 100%;
  width: 611px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.email-input-wrapper {
  width: 430px;
  height: 36px;
  background: #FFFFFF;
  color: #524A3E, 78%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.email-input {
  border: 0;
  width: 309px;
  padding: 0;
  margin: 0;
  line-height: 24px;
}

.email-input-button {
  width: 77px;
  height: 24px;
  background: #0061FE;
  border: 0;
  color: #F7F5F2;
  font-size: 14px;
  line-height: 18px;;
  padding: 0;
  margin: 0;
}

.copy.email-input-button {
  color: #1E1919;
  background: transparent;
  text-decoration: underline;
  cursor: pointer;
}

.sidebar-right-image {
  width: 415px;
  height: 684px;
  padding: 0;
  margin: 0;
}
.sidebar-right-image-confirmation {
  width: 350px;
  height: 350px;
  padding: 0;
  margin: 0;
}

.dropbox-button {
  width: 299px;
  height: 40px;
  background: #0061FE;
  border: 0;
  color: #F7F5F2;
  font-size: 16px;
  line-height: 20px;;
  padding: 0;
  margin: 0;
}

.sidebar-right-confirmation-text {
  width: 345px;
  text-align: center;
}

.email-folder-link {
  text-decoration: underline;
}
